import { Injectable, isDevMode } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import * as jwt_decode from 'jwt-decode';

// AIzaSyAt5EKL3uug5WSxx8AoVoz4z5mWy8Pq1hg

@Injectable({
  providedIn: 'root'
})

export class OurAuthService {

  private _registerUrl = "/api/auth/register";
  private _loginUrl = "/api/auth/login";
  private _oAuthgoogle="/api/auth/google";
  bat:string;
  uid:string;
  hid:string;
  mid:string;
  isValid:boolean=false;
  url = '';

  constructor(private http: HttpClient,private _router:Router) {
    if (isDevMode()) {
      this.url = "http://localhost:3000";
    }
  }


  registerUser(user) {
    return this.http.post<any>(this.url+this._registerUrl, user)
  }

  loginUser(user) {
    return this.http.post<any>(this.url+this._loginUrl, user)
  }

  loginOauthUser(user){
    console.log("here!!!");
    console.log("service ");
    console.log(this._oAuthgoogle);
    console.log(user);
    return this.http.post<any>(this.url+this._oAuthgoogle,user);
  }

  getTokenExpirationDate(token: string): Date {
    const decoded = jwt_decode(token);

    // console.log(decoded);

    if (decoded.exp === undefined) return null;

    const date = new Date(0);
    date.setUTCSeconds(decoded.exp);
    return date;
  }

  loggedIn(){
  let token= localStorage.getItem("token");

  if(!token) return false;
  const date = this.getTokenExpirationDate(token);

  if(date === undefined) return false;
  if(date.valueOf() < new Date().valueOf()){
    console.log("session expired");
    this.logout();
  }

  let tok=localStorage.getItem('token');
    if(tok)
    return tok;
    return null;
  }

  getToken(){
    return localStorage.getItem('token');
  }

  logout(){
    localStorage.removeItem('token');
    localStorage.removeItem('profilePic');
    this._router.navigate(['/auth/login']);
    this.bat=null;
    this.uid=this.hid=this.mid=null;
    this.isValid=false;

  }

  getName(){
    if(this.loggedIn()){
    var decode=jwt_decode(localStorage.getItem('token'));
    return decode.name;
    }
  }

  getMail(){
    if(this.loggedIn()){
      var decode=jwt_decode(localStorage.getItem('token'));
      return decode.email;
    }
  }

  getRole(){
    if(this.loggedIn()){
       var decode=jwt_decode(localStorage.getItem('token'));
      return decode.role;
      }
    return false;
  }

  validateAdmin(){
    if(this.loggedIn()){
       var decode=jwt_decode(localStorage.getItem('token'));
       var role=decode.role;
       if(role=="Admin") return true;
    }
    return false;
  }

  validateStudent(){
    if(this.loggedIn()){
       var decode=jwt_decode(localStorage.getItem('token'));
       var role=decode.role;
       if(role==="Student") return true;
    }
    return false;
  }

  validateFaculty(){
    if(this.loggedIn()){
      var decode=jwt_decode(localStorage.getItem('token'));
      var role=decode.role;
      if(role==="Faculty") return true;
   }
   return false;
  }

  getId(){
      var decode=jwt_decode(localStorage.getItem('token'));
      var id=decode._id;
      return id;
  }

  getProfileUrl(){
    return localStorage.getItem('profilePic') || "data:image/png;base64,";
  }

  setProfileData(bat:string,uid:string,hid:string,mid:string){
    this.bat=bat;
    this.uid=uid;
    this.hid=hid;
    this.mid=mid;
    if(this.bat && this.uid && this.hid && this.mid){
      this.isValid=true;
    }
  }

}
