import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { OurAuthService } from 'src/app/auth.service';

@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.scss']
})
export class NavComponent implements OnInit {

  isLogin:any;
  profilePic:any;

  constructor(public _authService: OurAuthService, private _router: Router) { }

  ngOnInit(): void {
    this.profilePic=localStorage.getItem('profilePic') || "data:image/png;base64,";

  }

  onClick(){
    this._router.navigate(['/profile']);
  }

}
